import React, {useEffect, useState} from "react";
import './ColorMode.scss'

export default (ChangeTheme) => {

    const [themeDark, changeTheme] = useState(true)

    useEffect(() => {
        const currentThemeColor = localStorage.getItem('theme-color')

        currentThemeColor === 'theme-dark' ? changeTheme(false) : changeTheme(true)

    }, [])

    const switchButton = () => {
        if(themeDark) {
            localStorage.setItem('theme-color', 'theme-light')
            changeTheme(false)
        } else {
            localStorage.setItem('theme-color', 'theme-dark')
            changeTheme(true)
        }
    }

    console.log('ChangeTheme', themeDark)

    return (
        <div className="toggle">
            <button onClick={switchButton}>
                {themeDark ? 'black' : 'light'}
            </button>
        </div>
    )
}